export default {
  data() {
    return {
      letter: [], // 字母集合
      clickLetter: "", // 点击的字母
      customers: [],
      showCode: "", // 显示字母
      startY: 0, // A坐标位置
      scrollHeight: 0,
      t_x: 0,
      t_y: 0
    };
  },
  methods: {
     touchstart: function(e) {
      this.t_x = e.touches[0].pageX;
      this.t_y = e.touches[0].pageY; // 获取触摸时的原点
      // timeInterval = setInterval(function() {
      //   times++;
      // }, 50);
    },
    touchend: function(e, ind) {
      let touchMoveX = e.changedTouches[0].pageX;
      let touchMoveY = e.changedTouches[0].pageY;
      let dataList = this.dataList;
      let tmX = touchMoveX - this.t_x;
      let tmY = touchMoveY - this.t_y;
      let absX = Math.abs(tmX);
      let absY = Math.abs(tmY);
      if (absX > 2 * absY && absX > 5) {
        if (tmX < 0) {
          dataList.filter((v, i) => {
            uni.showModal({
              title: '',
              
              content: '你确定解除好友关系吗？',
              success: function (res) {
                if (res.confirm) {
                  console.log('用户点击确定');
                } else if (res.cancel) {
                  console.log('用户点击取消');
                }
              }
            });
          });
        } else {
          
        }
      }
      this.dataList = dataList;
      this.t_y = 0;
      this.t_x = 0;
    },
    letterTap(e) {
      const item = e.currentTarget.dataset.item;
      this.clickLetter = item;
      this.showCode = item == "NULL" ? "#" : item;

      
        wx.createSelectorQuery()
        .select("#" + item.toLocaleUpperCase())
        .boundingClientRect(res => {
          if (res.top) {
            wepy.pageScrollTo({
              scrollTop: res.top + this.scrollHeight - 102,
              duration: 0
            });
          }
        }).exec();

      let time = setTimeout(() => {
        this.showCode = "";
        clearTimeout(time);
      }, 1000);
    },
    getData(key) {
      let params = {
        consignee: key
      };
      this.$acFrame.HttpService.customers(params)
        .then(res => {
          this.renderLetter(res);
        })
        .catch(err => {
          console.error(err);
        });
    },

    // 渲染字母
    renderLetter(res) {
      if (!(res && res.keyList)) {
        this.showNoImg = true;
        this.letter = [];
        this.customers = [];
        return;
      }
      this.showNoImg = false;
      this.letter = res.keyList;
      this.customers = res.types;
      this.$apply();
      wx.createSelectorQuery()
        .select(".letter-item." + this.letter[0])
        .boundingClientRect(res => {
          this.startY = res.top;
        }).exec();
    }
  }
};
